/*
* Noto Sans Egyptian Hieroglyphs (Egyptian Hieroglyphs) http://www.google.com/fonts/earlyaccess
*/
@font-face {
  font-family: "Noto Sans Egyptian Hieroglyphs";
  font-style: normal;
  font-weight: 400;
  src: url(//fonts.gstatic.com/ea/notosansegyptianhieroglyphs/v1/NotoSansEgyptianHieroglyphs-Regular.eot);
  src: url(//fonts.gstatic.com/ea/notosansegyptianhieroglyphs/v1/NotoSansEgyptianHieroglyphs-Regular.eot?#iefix)
      format("embedded-opentype"),
    url(//fonts.gstatic.com/ea/notosansegyptianhieroglyphs/v1/NotoSansEgyptianHieroglyphs-Regular.woff2)
      format("woff2"),
    url(//fonts.gstatic.com/ea/notosansegyptianhieroglyphs/v1/NotoSansEgyptianHieroglyphs-Regular.woff)
      format("woff"),
    url(//fonts.gstatic.com/ea/notosansegyptianhieroglyphs/v1/NotoSansEgyptianHieroglyphs-Regular.ttf)
      format("truetype");
}
/*
* Noto Sans Phoenician (Phoenician) http://www.google.com/fonts/earlyaccess
*/
@font-face {
  font-family: "Noto Sans Phoenician";
  font-style: normal;
  font-weight: 400;
  src: url(//fonts.gstatic.com/ea/notosansphoenician/v1/NotoSansPhoenician-Regular.eot);
  src: url(//fonts.gstatic.com/ea/notosansphoenician/v1/NotoSansPhoenician-Regular.eot?#iefix)
      format("embedded-opentype"),
    url(//fonts.gstatic.com/ea/notosansphoenician/v1/NotoSansPhoenician-Regular.woff2)
      format("woff2"),
    url(//fonts.gstatic.com/ea/notosansphoenician/v1/NotoSansPhoenician-Regular.woff)
      format("woff"),
    url(//fonts.gstatic.com/ea/notosansphoenician/v1/NotoSansPhoenician-Regular.ttf)
      format("truetype");
}

* {
  font-family: "Noto Sans Phoenician", "Noto Sans Egyptian Hieroglyphs",
    sans-serif;
}
